import { FileInfo, FileInfoEnum } from './types';
import { ActiveFileHandlerInterface, FileHandlerConfig, FileHandlerInterface } from './file-handler.interface';
import { GenericActiveFileActiveFileHandler } from './generic-active-file.active-file-handler';

export interface DummyConfig extends FileHandlerConfig {
  type: 'dummy';
  dummyFiles: string[];
}

export class DummyFilehandler implements FileHandlerInterface {

  constructor(
    private readonly dummyConfig: DummyConfig
  ) {
    console.log('Dummy File handler setup!');
  }

  async list(): Promise<FileInfo[]> {
    return (this.dummyConfig.dummyFiles ?? []).map((file) => {
      return {
        name: file,
        size: 0,
        modifyTime: new Date(),
        type: FileInfoEnum.FILE,
      };
    });
  }

  async readFile(): Promise<ActiveFileHandlerInterface> {
    const randomData = Math.random().toString(36).substring(7);
    const buffer = Buffer.from(randomData);
    return new GenericActiveFileActiveFileHandler(buffer);
  }

  async writeFile(): Promise<boolean> {
    return true;
  }

  async deleteFile(): Promise<boolean> {
    return true;
  }

  async fileExists(): Promise<boolean> {
    return true;
  }

  async init(): Promise<void> {
    console.log('DummyFilehandler initialized');
  }
}
